<template>
  <div class="preview">
    <NavBar back :title="data.id" />
    <van-collapse v-model="activeNames">
      <div class="step">
        <van-collapse-item name="1">
            <template #title>
              <Title title="跟踪" :style="{margin: 0,padding:0,border: 0}" />
            </template>
            <van-steps direction="vertical" inactive-icon="checked" :active-icon="stepIcon" :active="0" active-color="#38f">
              <van-step v-for="(item, i) in historyList" :key="i">
                <h3>{{item.activityName}}</h3>
                <p>{{item.comment}}</p>
                <p v-if="item.assigneeName"><van-icon name="user-o" /><span>{{item.assigneeName}}</span></p>
                <p v-if="item.durationTime"><van-icon name="clock-o" /><span>{{ item.durationTime}}</span></p>
              </van-step>
            </van-steps>
        </van-collapse-item>
      </div>
    </van-collapse>
    <div class="detail">
      <van-cell-group>
        <Title title="详情" />
        <van-cell title="报修人" :value="data.user.name" />
        <van-cell title="学工号" :value="data.user.loginName" />
        <van-cell title="所属部门" :value="data.user.depart.text" />
        <van-cell title="联系方式" :value="data.user.phone" />
        <van-cell title="所属校区" :value="data.department.school.text" />
        <van-cell title="报修分类" :value="data.department.area.text" />
        <van-cell title="工单标题" :value="data.title" />
        <van-cell title="报修内容">
          {{data.content}}
        </van-cell>
        <van-cell title="报修地点" :value="data.location" />
        <van-cell title="附件">
          <template>
            <div class="imgs">
              <van-uploader v-model="data.issue" multiple :deletable="false" :show-upload="false" />
            </div>
          </template>
        </van-cell>
      </van-cell-group>
      <van-cell-group>
        <van-cell v-if="data.assignClass.className" title="分配班组" :value="data.assignClass.className" />
        <van-cell v-if="data.assignClass.leader" title="班组长" :value="data.assignClass.leader" />
        <van-cell v-if="data.worker.name" title="维修员" :value="data.worker.name" />
        <van-cell v-if="data.worker.phone" title="联系方式" :value="data.worker.phone" />
        <van-cell v-if="showWorkAttachment" title="维修附件">
          <template>
            <div class="imgs">
              <van-uploader v-model="data.worker.attachment" multiple :deletable="false" :show-upload="false" />
            </div>
          </template>
        </van-cell>
      </van-cell-group>
      <van-cell-group v-if="rate.grade">
        <van-cell title="评价">
          <template>
            <van-rate
              count="3"
              readonly
              v-model="rate.grade"
              :size="25"
              color="#3ca2e0"
              void-icon="star"
              void-color="#eee"
            />
          </template>
        </van-cell>
        <van-cell title="评价说明" :value="rate.content" />
      </van-cell-group>
    </div>
  </div>
</template>
<script>
import {Cell, CellGroup, Steps, Step, Icon, Collapse, CollapseItem, Uploader, Rate} from 'vant'
import NavBar from '@/components/NavBar'
import Title from '@/components/Title'
import dayjs from 'dayjs'
import axios from 'axios'
import {formatMinutes} from '@/helper/timeFormat'
import {detailInfoFormat} from '@/helper/dataFormat'
import { apiHost } from '../config'

const sessionId = localStorage.getItem('sessionId')

export default {
  props: ['procInsId'],
  data() {
    return {
      activeNames: ['1'],
      historyList: [],
      data: {
        user: {
          depart: {},
        },
        worker: {
          attachment: []
        },
        assignClass: {},
        department: {
          school: {},
          area: {},
        },
        issue: []
      },
      stepIcon: '',
      rate: {},
    }
  },
  components: {
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Steps.name]: Steps,
    [Step.name]: Step,
    [Icon.name]: Icon,
    [Collapse.name]: Collapse,
    [CollapseItem.name]: CollapseItem,
    [Uploader.name]: Uploader,
    [Rate.name]: Rate,
    NavBar,
    Title,
  },
  computed: {
    timeDiff: function() {
      return dayjs(this.startTime) + '-' + (dayjs(this.endTime))
    },
    showWorkAttachment: function() {
      return this.data.worker.attachment.length > 0
    }
  },
  mounted() {
    const id = this.$route.params.id
    this.getHistory(id)
    this.getDetail(id)
    this.getRate(id)
  },
  filters: {
    timeDiff: function(startTime, endTime) {
      return formatMinutes(dayjs(endTime).diff(dayjs(startTime), 'minute'))
    }
  },
  methods: {
    onBack() {
      this.show = false;
      this.$router.back(-1)
    },
    setStepIcon: function(data) {
      this.stepIcon =  data.activityName === '结束' ? 'checked' : 'clock';
    },
    getHistory(procInsId) {
      axios.get(`${apiHost}/repair/a/wechat/api/task/histoicFlow`, {
        params: {
          procInsId
        }
      }).then(res =>{
        this.historyList = res.data.body.histoicFlowList.reverse().filter(item => !!item.activityName)
        this.setStepIcon(this.historyList[0]);
      }).catch(err => {
        console.log(err)
      })
    },
    getDetail(procInsId) {
      axios.get(`${apiHost}/repair/a/wechat/api/rprorder/getRprOrder;JSESSIONID=${sessionId}`, {
        params: {
          procInsId
        }
      }).then(res => {
        this.data = detailInfoFormat(res.data.body)
        console.log('ddddd', this.data)
      }).catch(err => {
        console.log(err)
      })
    },
    getRate(id) {
      axios.get(`${apiHost}/repair/a/wechat/api/evaluate/getEvaluateInfo;JSESSIONID=${sessionId}`, {
        params: {
          procInstId: id
        }
      }).then(res => {
        if(res.data.body.evaluate) {
          this.rate = {
            id: res.data.body.evaluate.id,
            flag: res.data.body.evaluate.orderFlag,
            grade: parseInt(res.data.body.evaluate.grade, 10),
            content: res.data.body.evaluate.content
          }
        }
        
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>
<style lang="scss">
  .step {
    padding: 0 8px;
    background-color: #fff;
    h3 {
      margin: 0;
    }
    .van-icon {
      vertical-align: middle;
      margin-right: 5px;
      font-size:16px;
      & + span {
        vertical-align: middle;
      }
    }
    .van-step__icon--active {
      font-size: 24px;
    }
    p {
      margin: 8px 0;
    }
  }
  .detail {
    .van-cell__title {
      width: 100px;
      flex: none;
    }
    .van-cell__value {
      text-align: left;
    }
    
    .imgs {
      vertical-align: top;
    }
  }
  
</style>